// src/CustomCard.ts
export class CustomCard extends HTMLElement {
    constructor() {
      super();
      const shadow = this.attachShadow({ mode: 'open' });
  
      const wrapper = document.createElement('div');
      wrapper.setAttribute('class', 'card-wrapper');
  
      const title = this.getAttribute('title') || 'Untitled';
      const description = this.getAttribute('description') || 'No description available';
  
      wrapper.innerHTML = `
        <style>
          .card-wrapper {
            border: 1px solid #ddd;
            border-radius: 8px;
            padding: 16px;
            margin: 8px;
            box-shadow: 2px 2px 12px rgba(0,0,0,0.1);
          }
          h3 {
            margin-top: 0;
          }
          p {
            color: #555;
          }
        </style>
        <h3>${title}</h3>
        <p>${description}</p>
      `;
  
      shadow.appendChild(wrapper);
    }
  }
  
  export function defineCustomCard() {
    if (!customElements.get('custom-card')) {
      customElements.define('custom-card', CustomCard);
    }
  }
  