// src/App.tsx
import React, { useEffect } from 'react';
import './App.css';
import './CustomCard';  // Import the custom web component

function App() {
  useEffect(() => {
    // Register the custom web component
    import('./CustomCard').then(module => {
      module.defineCustomCard();
    });
  }, []);

  return (
    <div className="App">
      <header className="App-header">
        <nav>
          <ul className="nav-list">
            <li><a href="#home">Home</a></li>
            <li><a href="#about">About</a></li>
            <li><a href="#projects">Projects</a></li>
            <li><a href="#contact">Contact</a></li>
          </ul>
        </nav>
      </header>
      
      <main>
        <section id="home">
          <h1>Welcome to My Personal Webpage</h1>
          <p>Hi, I'm Philipp Wagner, a passionate developer...</p>
        </section>

        <section id="about">
          <h2>About Me</h2>
          <p>This is where you can describe yourself, your journey, skills, and background.</p>
        </section>

        <section id="projects">
          <h2>Projects</h2>
          {/* Using the custom card component */}
          <custom-card title="Project 1" description="A brief description of project 1"></custom-card>
          <custom-card title="Project 2" description="A brief description of project 2"></custom-card>
        </section>
      </main>

      <footer>
        <p>&copy; 2024 Philipp Wagner. All rights reserved.</p>
      </footer>
    </div>
  );
}

export default App;

